* {
  box-sizing: border-box;
}

:host {
  display: block;
  border: var(--pfe-primary-details--Border, var(--pfe-theme--ui--border-width--sm, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--ui--border--lighter, #d2d2d2));
  overflow: hidden;
}

:host([hidden]) {
  display: none;
}

#wrapper {
  display: grid;
  grid-template-columns: var(--pfe-primary-details--GridTemplateColumns, 1fr 2fr);
  align-items: stretch;
  justify-content: stretch;
  overflow: hidden;
  transition: transform 0.25s ease-in-out;
  will-change: transform;
  backface-visibility: visible;
}

@media (prefers-reduced-motion) {
  #wrapper {
    transition: none;
    will-change: auto;
  }
}

:host([breakpoint='compact']) #wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  gap: 0;
  width: 200%;
}

:host([breakpoint='compact']) #wrapper.active {
  transform: translateX(-50%);
}

:host([breakpoint='compact']) #details-nav, :host([breakpoint='compact'])
#details-nav__footer {
  align-self: stretch;
  justify-self: stretch;
  grid-row: 1;
  grid-column: 1;
}

:host([breakpoint='compact']) #details-nav__footer {
  grid-row: 2;
}

#details-nav {
  display: flex;
  flex-direction: column;
  margin: 0 2px;
  padding: 9px 0;
  border-right: var(--pfe-primary-details--Border, var(--pfe-theme--ui--border-width--sm, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--ui--border--lighter, #d2d2d2));
}

:host([breakpoint='compact']) #details-nav {
  border: 0;
}

:host([breakpoint='compact']) #details-nav__footer {
  grid-row: 2;
}

::slotted([slot="details-nav"]) {
  position: relative;
  display: block !important;
  appearance: none !important;
  background: transparent !important;
  border: 0 !important;
  margin: 0 !important;
  padding: 0.5em 1.5em 0.5em 0.75em !important;
  font: inherit;
  line-height: 1.5;
  text-align: left !important;
  color: var(--pfe-primary-details__nav--Color, var(--pfe-theme--color--text, #151515)) !important;
  cursor: pointer !important;
}

::slotted([slot="details-nav"]):before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0%;
  margin-right: 16px;
  display: block;
  width: 0.75em;
  height: 0.75em;
  box-shadow: inset -0.1875em -0.1875em 0 0.0625em #000;
  transform: translate(-50%, -50%) scale(0.6) rotate(314deg);
}

:host([breakpoint='desktop']) ::slotted([slot="details-nav"]):before {
  content: none;
}

::slotted([aria-selected="true"]),
::slotted([aria-expanded="true"]) {
  color: var(--pfe-primary-details__nav--Color--active, var(--pfe-theme--color--link, #06c)) !important;
  background: var(--pfe-primary-details__nav--Background--active, #f0f0f0) !important;
}

#details-wrapper {
  padding: 0.5em 1.5em;
  background: var(--pfe-primary-details__details--Background, var(--pfe-theme--color--surface--lightest, #fff));
}

:host([breakpoint='compact']) #details-wrapper {
  grid-row: 1 / span 2;
  grid-column: 2;
  padding-top: 0;
}

:host([consistent-height]) #details-wrapper {
  display: flex;
}

@supports (display: grid) {
  :host([consistent-height]) #details-wrapper {
    display: grid;
    grid-template: 1fr / 1fr;
    justify-items: start;
    align-items: start;
  }
}

:host([consistent-height][breakpoint='compact']) #details-wrapper:before {
  content: '';
  display: block;
  grid-row: 1;
  grid-column: 1;
  align-self: end;
  border-bottom: var(--pfe-primary-details--Border, var(--pfe-theme--ui--border-width--sm, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--ui--border--lighter, #d2d2d2));
  width: calc(100% + 3em);
  margin-left: -1.5em;
}

#details-wrapper__header {
  display: none;
  grid-row: 1 / span 1;
  grid-column: 1 / span 1;
  align-self: start;
  justify-self: stretch;
}

:host([breakpoint='compact']) #details-wrapper__header {
  display: flex;
  align-items: flex-start;
}

#details-wrapper__back {
  display: none;
  position: relative;
  align-self: stretch;
  border: 0;
  margin: 2px 0 0;
  background: transparent;
  font-size: inherit;
  font-family: inherit;
  font-weight: bold;
  cursor: pointer;
  text-align: left;
}

#details-wrapper__back:focus {
  outline: 1px dashed #000 !important;
  outline-width: 2px !important;
}

:host([breakpoint='compact']) #details-wrapper__back {
  display: block;
  visibility: hidden;
}

:host([breakpoint='compact']) .active #details-wrapper__back {
  visibility: visible;
  width: 100%;
  padding-left: 16px;
}

#details-wrapper__back:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0.5em;
  display: block;
  width: 0.75em;
  height: 0.75em;
  box-shadow: inset -0.1875em -0.1875em 0 0.0625em #000;
  transform: translate(-50%, -50%) scale(0.6) rotate(135deg);
}

#details-wrapper__heading {
  margin: 0;
  padding: 0;
  font-size: inherit;
  line-height: 1.5;
  padding: 0.5em 0;
}

:host([consistent-height]) ::slotted([slot="details"]) {
  display: block !important;
  grid-row: 1/span 1 !important;
  grid-column: 1/span 1 !important;
}

:host([consistent-height][breakpoint='compact']) ::slotted([slot="details"]) {
  grid-row: 2/span 1 !important;
}

::slotted([hidden]) {
  visibility: hidden !important;
  position: absolute !important;
}

:host([consistent-height]) ::slotted([hidden]) {
  position: static !important;
}

::slotted([slot="details-nav--footer"]) {
  border-right: var(--pfe-primary-details--Border, var(--pfe-theme--ui--border-width--sm, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--ui--border--lighter, #d2d2d2)) !important;
}

::slotted(.is-focused:focus),
::slotted(.is-focused:hover) {
  outline: 1px dashed #000 !important;
  outline-width: 2px !important;
}
